import { ToastService } from './../../../../_services/_toast/_toast.service';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { DbService, UserService } from 'src/app/_services';

@Component({
  selector: 'app-addProductType',
  templateUrl: './addProductType.component.html',
  styleUrls: ['./addProductType.component.css'],
})
export class AddProductTypeComponent implements OnInit {
  error: any;
  productTypeForm: FormGroup;
  businesses: any;
  constructor(
    private fb: FormBuilder,
    private db: DbService,
    private userService: UserService,
    private toastService: ToastService
  ) {}

  ngOnInit() {
    this.initForm();
    this.getBusinesses();
  }

  public tryAddProductType(): void {
    if (this.productTypeForm.valid) {
      this.addProductType();
    } else {
      this.toastService.showError('Missing required fields');
    }
  }

  private async getBusinesses() {
    this.businesses = await this.db.getBusinessessByUserId(
      this.userService.currentUserValue.id
    );
  }

  private async addProductType(): Promise<void> {
    const productType = this.productTypeForm.value;
    try {
      await this.db.addProductType(productType);
      this.toastService.showSuccess(
        `Product type '${productType.name}' added successfully`
      );
    } catch (error) {
      this.toastService.showError(error);
    }
  }

  private initForm() {
    this.productTypeForm = this.fb.group({
      name: [[], [Validators.required]],
      businessIds: [[], [Validators.required]],
    });
  }
}
